import PhoneImage from "./undraw_modern_design_v-3-wv.svg";

function Step(props) {
    return(
        <div className="step-function">
            <div><span className="step">STEP {props.step}</span><span className="self">SELF ANALYSIS</span></div>
        </div>
    );
}

const Hero = () => {
    return(
        <div>
            <div class="row container">

            <div class="col-lg-6 left-content">
            <h1 class="heading">How we can Help</h1>
            <p className="heading-detail">Habitant morbi tristique senectus et netus et malesuada. Magnis dis parturient montes nascetur ridiculus mus. Pretium fusce id velit ut tortor pretium viverra suspendisse potenti. Tortor posuere ac ut consequat semper viverra nam libero justo.</p>
            <Step step="1" />
            <Step step="2" />
            <Step step="3" />
            <Step step="4" />
            </div>
                <div class="col-lg-6">
                    <img class="title-image" src={PhoneImage} alt="phone-image"></img>
                </div>
            </div>
        </div>
        
    );
};

export default Hero;